.content-body {
    &.pickupBasket {
        .content-wrapper {
            .title-section {
                width: 100%;
                padding: 10px 0 20px 19px;
                border-bottom: 1px solid #eee;
                .main-box {
                    margin-bottom: 20px;
                    .nickName {
                        color: #000;
                        @include fontSize(18px,300);
                        line-height: 27px;
                        margin-bottom: 1px;
                        span {
                            color: #000;
                            @include fontSize(18px,500);
                            line-height: 27px;
                            margin-right: 5px;
                        }
                    }
                    .totalPrice {
                        color: $mainColor;
                        @include fontSize(12px,400);
                        line-height: 18px;
                        span {
                            color: $mainColor;
                            @include fontSize(35px,700);
                            line-height: 42px;
                            margin-right: 5px;
                        }
                    }
                }
                .sub-box {
                    .sub-content {
                        margin-bottom: 5px;
                        &:last-child {
                            margin-bottom: 0;
                        }
                        &:after {
                            @include clearBoth;
                        }
                        .reward-name {
                            &.noGoods {
                                text-decoration: line-through;
                            }
                            @include fontSize(12px,300);
                            line-height: 18px;
                            color: #000;
                            float: left;
                            
                        }
                        .reward-price {
                            @include fontSize(12px,700);
                            line-height: 14px;
                            color: #333;
                            float: right;
                        }
                    }
                }
            }
            .content-section {
                margin-top: 20px;
                .btn-area {
                    margin-bottom: 10px;
                    .checkbox-wrap {
                        position: relative;
                        .checkbox-custom {
                            position: absolute;
                            left: 0;
                            top: 2px;
                        }
                        .checkbox-label {
                            width: 100%;
                            display: block;
                            padding-left: 25px;
                            @include fontSize(12px,500);
                            line-height: 18px;
                            color: #000;
                        }
                        .alldelete-btn {
                            float: right;
                            @include fontSize(11px,300);
                            color: #333;
                            line-height: 17px;
                        }
                    }
                }
                .content-area {
                    .none-list {
                        @include fontSize(12px,500);
                        color: #999;
                        text-align: center;
                        line-height: calc( 100vh - 324px );
                    }
                    .content-wrap {    
                        &.withoutGoods {
                            .img-box {
                                p {
                                    width: 80px;
                                    height: 25px;
                                    color: #fff;
                                    background: rgba(#000, .8);
                                    @include fontSize(12px,500);
                                    line-height: 18px;
                                    text-align: center;
                                    position: absolute;
                                    bottom: 0;
                                    padding: 3px 0;
                                }
                            }
                            .word-box {
                                opacity: 0.5;
                            }                            
                        }               
                        @include flexCenter;
                        padding: 20px 0 20px 25px;
                        border-bottom: 1px solid #eeeeee;
                        position: relative;
                        .checkbox-box {
                            position: absolute;
                            top: 20px;
                            left: 0;
                            margin-right: 10px;
                        }
                        .img-box {
                            background: url(../img/image_09.png) center center;
                            width: 80px;
                            height: 80px;
                            margin-right: 10px;
                            position: relative;
                        }
                        .word-box {
                            height: 80px;
                            width: calc( 100% - 90px);
                            position: relative;   
                            &.cancle {
                                opacity: 0.5;
                            }
                            .toTop {      
                                margin-bottom: 5px; 
                                position: relative;                    
                                &:after {
                                    @include clearBoth;
                                }
                                p {
                                    float: left;                                    
                                    span {
                                        display: inline-block;
                                        color: #fff;
                                        @include fontSize(12px,300);
                                        line-height: 18px;
                                        background: #000;
                                        padding: 3px 9px;
                                        border-radius: 12px;
                                        margin-right: 5px;
                                    }
                                }
                                button {
                                    border: 0;
                                    background: none;
                                    position: absolute;
                                    right: 0;
                                    height: 10px;
                                    width: 10px;
                                    @include flexAllCenter;
                                }
                            }
                            .menuInfo {
                                @include fontSize(14px,500);
                                color: #333;
                                line-height: 20px;
                                p {
                                    width: calc( 100% - 5px );
                                    @include wordClear;
                                    line-height: 20px;
                                }
                            }
                            .price { 
                                width: 100%;
                                bottom: 2px;
                                @include flexCenter;
                                position: relative;
                                .btnBlock {     
                                    &:after {
                                        @include clearBoth;
                                    }
                                    >button {
                                        float: left;
                                        background: none;
                                        border: 1px solid #eee;
                                        width: 30px;
                                        height: 28px;
                                    }
                                    .goodsAmount {
                                        float: left;
                                        width: 47px;
                                        height: 28px;
                                        border: 1px solid #eee;
                                        @include flexAllCenter;
                                        span {
                                            color: #333;
                                            @include fontSize(12px,400);
                                            line-height: 14px;
                                        }
                                    }
                                }
                                .priceNum {                                    
                                    color: #333;
                                    @include fontSize(12px,400);
                                    line-height: 18px;
                                    position: absolute;
                                    right: 0;
                                    span {
                                        color: #333;
                                        @include fontSize(15px,700);
                                        line-height: 18px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }        
        input[type="checkbox"] {
            display:none;
        }
        input[type="checkbox"] + label .checkbox-custom {
            display:inline-block;
            width: 15px;
            height: 15px;
            vertical-align:middle;
            cursor:pointer;
            border: 1px solid #c9c9c9;
            background: #fff;
            -webkit-background-size: 100%; background-size: 100%;
        }
        input[type="checkbox"]:checked + label .checkbox-custom {
            width: 15px;
            height: 15px;
            border: none;
            background: url(../img/icon_checkbox_on.png) center center no-repeat;
            -webkit-background-size: 100%; background-size: 100%;
        }
        input[type="checkbox"]:disabled + label .checkbox-custom {
            background: rgba( #c9c9c9, .5);
            border: none;
        }     
    }
    .btn-wrapper {
        @include flexAllCenter;
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 60px;
        background: $mainColor;
        color: #fff;
        line-height: 22px;
        @include fontSize(15px,500);
        span {
            color: #fff;
            line-height: 22px;
            @include fontSize(15px,500);
        }
    }
}



