.index-box {
    .popup-wrapper {       
        background: rgba(0,0,0,.5);
        position: fixed;
        width: 100%;
        height: 100vh;
        bottom: 0;
        padding: 0 30px;
        @include flexCenter;
        .popup-inner {
            background: #fff;
            width: 100%;
            position: relative;
            .close-box {
                position: absolute;
                top: 0;
                right: 0;
                height: 40px;
                width: 40px;
                @include flexAllCenter;
                @include btnNone;
                z-index: 2;
                img {
                    width: 10px;
                }
            }
            .header-section {
                padding: 30px 0 20px;
                p {
                    @include fontSize(17px,700);
                    line-height: 25px;
                    text-align: center;
                }
            }
        }
        &.login-wrapper {
            padding: 0;
            .popup-inner {
                width: 100%;
                height: 100vh;
                overflow: scroll;
                .close-box {
                    width: 49px;
                    height: 49px;
                    img {
                        width: 19px;
                    }
                }
                .header-section {
                    height: 54px;
                    @include flexCenter;
                    padding: 0;
                    padding-left: 15px;
                }
                .content-section {
                    min-height: calc( 100vh - 181px );
                    >ul {
                        @include flexCenter;
                        border-bottom: 1px solid #eee;
                        >li {
                            text-align: center;
                            height: 42px;
                            width: 50%;
                            @include flexAllCenter;
                            @include fontSize(15px,400);
                            color: #999;
                            position: relative;
                            @keyframes tabLine {
                                0% {
                                    width: 0;
                                }
                                100% {
                                    width: 100%;
                                }
                            }
                            &:before {
                                content: "";
                                position: absolute;
                                right: 0;
                                background: #edf0f3;
                                height: 9px;
                                width: 1px;
                                top: 50%;
                                transform: translate(0,-50%);
                            }
                            &.active {
                                @include fontSize(15px,700);
                                color: #000;
                                translate: .3s;
                                &:after {
                                    content: "";
                                    width: 100%;
                                    position: absolute;
                                    bottom: -1px;
                                    height: 3px;
                                    background: #000;
                                    left: 50%;
                                    transform: translate(-50%,0);
                                    animation: tabLine .3s;
                                }
                            }
                            &:last-child {
                                &:before {
                                    display: none;
                                }
                            }
                        }
                    }
                    .target-wrap {
                        .tabTarget {
                            display: none;
                            &.active {
                                display: block;
                            }
                            .user-area {
                                padding-top: 19px;
                                position: relative;
                                &.logout-form {
                                    height: 513px;
                                }
                                &.login-form {
                                    height: 595px;
                                }
                                .user-header {
                                    margin-bottom: 20px;
                                    padding: 0 34px;
                                    &.logOut {
                                        margin-bottom: 54px;
                                    }
                                    .user-name {
                                        margin-bottom: 5px;
                                        @include fontSize(24px,400);
                                        line-height: 36px;
                                        strong {
                                            margin-right: 5px;
                                        }
                                        strong,span {
                                            @include fontSize(24px,700);
                                        }
                                    }
                                    pre {
                                        margin-bottom: 5px;
                                        @include fontSize(24px,400);
                                        line-height: 36px;
                                        margin-bottom: 25px;

                                    }
                                    .btn-area {
                                        @include fontSize(14px,400);
                                        line-height: 20px;
                                        &:after {
                                            @include clearBoth;
                                        }
                                        .loginBtn {
                                            color: #000;
                                            border: 1px solid #000;
                                            background: #fff;
                                            width: calc( 100% / 2 - 5px );
                                            height: 35px;
                                            float: left;                                            
                                        }
                                        .joinBtn {
                                            color: #fff;
                                            border: 0;
                                            background: #000;
                                            width: calc( 100% / 2 - 5px );
                                            height: 35px;
                                            float: right;    
                                        }
                                    }
                                    .edit-info {
                                        @include btnNone;
                                        @include flexCenter;
                                        line-height: 18px;
                                        @include fontSize(12px,100);
                                        img {
                                            width: 8px;
                                        }
                                    }
                                }
                                .user-items {
                                    padding: 0 15px;
                                    margin-bottom: 5px;
                                    &:after {
                                        @include clearBoth;
                                    }
                                    button {
                                        float: left;
                                        @include btnNone;
                                        width: 25%;
                                        height: 70px;
                                        margin-bottom: 15px;
                                        img {
                                            width: 38px;
                                        }
                                    }
                                }
                                .userBar-items {                                    
                                    ul {                                        
                                        li {
                                            @include fontSize(12px,500);
                                            line-height: 18px;
                                            color: #000;
                                            padding: 13px 15px;
                                            border-bottom: 1px solid #eee;
                                            position: relative;
                                            &:first-child {
                                                border-top: 1px solid #eee;
                                            }
                                            img {
                                                position: absolute;
                                                top: 50%;
                                                transform: translate(0,-50%);
                                                right: 15px;
                                            }
                                            .custom-box {
                                                position: absolute;
                                                top: 50%;
                                                transform: translate(0,-50%);
                                                right: 15px;
                                                height: 22px;
                                                input[type="checkbox"] {
                                                    display:none;
                                                }
                                                input[type="checkbox"] + label {
                                                    background: #eee;
                                                    width: 37px;
                                                    height: 22px;
                                                    border-radius: 11px;
                                                    transition: .3s;
                                                    position: relative;
                                                }
                                                input[type="checkbox"]:checked + label {
                                                    background: #000;
                                                    transition: .3s;
                                                }
                                                input[type="checkbox"] + label .checkbox-custom {
                                                    width: 18px;
                                                    height: 18px;
                                                    border-radius: 18px;
                                                    background: #fff;
                                                    position: absolute;
                                                    top: 2px;
                                                    left: 2px;
                                                    transition: .3s;
                                                }
                                                input[type="checkbox"]:checked + label .checkbox-custom {
                                                    width: 18px;
                                                    height: 18px;
                                                    border-radius: 18px;
                                                    top: 2px;
                                                    left: 17px;
                                                    transition: .3s;
                                                }
                                            }                               
                                        }
                                    }
                                }
                                .telephone-item {
                                    position: absolute;
                                    bottom: 33px;
                                    width: 100%;
                                    padding-left: 15px;
                                    .phoneNum {
                                        @include fontSize(20px,700);
                                        line-height: 20px;
                                        color: #000;
                                        margin-bottom: 5px;
                                    }
                                    .operatingTime {
                                        @include fontSize(11px,300);
                                        line-height: 17px;
                                        color: #000;
                                        margin-bottom: 2px;
                                        span {
                                            @include fontSize(12px,300);
                                            color: #000;
                                            line-height: 14px;
                                        }
                                    }
                                    .copyRight {
                                        @include fontSize(10px,300);
                                        color: #999;
                                        line-height: 13px;
                                    }
                                    .btn-section {
                                        margin-top: 18px;
                                        button {
                                            @include fontSize(12px,300);
                                            line-height: 18px;
                                            color: #999;
                                            background: none;
                                            border: none;
                                            height: 35px;
                                            border: 1px solid #eee;
                                            width: calc( 100% - 30px );                                    
                                        }
                                    }
                                }               
                            }                        
                        }
                    }
                }
            }
        }
        &.basic-wrapper {
            .popup-inner {              
                .content-section {
                    p {
                        color: #000;
                        @include fontSize(14px,400);
                        text-align: center;
                        padding: 40px 15px;       
                        word-break: keep-all;                   
                    }
                }
                .footer-section {
                    &.one-btn {
                        .go {
                            width: 100%;
                        }
                    }
                    &:after {
                        @include clearBoth;
                    }
                    .continue {
                        background: none;
                        border: 0;
                        color: #fff;
                        @include fontSize(14px,400);
                        line-height: 20px;
                        background: #000;
                        height: 45px;
                        width: 50%;
                        float: left;                            
                    }
                    .go {
                        background: none;
                        border: 0;
                        color: #fff;
                        @include fontSize(14px,400);
                        line-height: 20px;
                        background: $mainColor;
                        height: 45px;
                        width: 50%;
                        float: right;
                    }
                }
            }            
        }
        &.review-wrapper {
            .popup-inner {
                .content-section {
                    padding: 0 30px 40px;
                    .star-box {
                        width: 100%;                        
                        margin-bottom: 10px;
                        &:after {
                            @include clearBoth;
                        }
                        .starImg {
                            float: left;
                            img {
                                width: 19px;
                                height: 18px;
                                float: left;
                                margin-right: 1px;
                                &:last-of-type {
                                    margin-right: 0;
                                }
                            }
                        }
                        .nickname {
                            float: right;
                            p {
                                @include fontSize(10px,300);
                                line-height: 20px;
                                color: #000;
                                span {
                                    @include fontSize(12px,700);
                                    line-height: 20px;
                                    color: #000;
                                }
                            }
                        }                     
                    }
                    .content-box {          
                        max-height: 330px;
                        overflow: scroll;
                        .img-area {
                            margin-bottom: 15px;
                            text-align: center;
                            img {
                                max-width: 100%;
                            }
                        }
                        p {
                            @include fontSize(12px,400);
                            line-height: 20px;
                            color: #333;
                        }
                    }
                }
            }
        }
        &.complete-wrapper {
            .popup-inner {
                .content-section {
                    padding: 0 15px;
                    max-height: calc( 100vh - 192px );
                    overflow-y: scroll;
                    .content-area {
                        padding-bottom: 25px;
                        .info-box {
                            border-bottom: 1px solid #eeeeee;
                            padding: 15px 0 10px;
                            &:first-of-type {
                                padding-top: 0;    
                            }
                            &:last-of-type {
                                border-bottom: 0;
                                padding-bottom: 0;
                            }
                        }
                        .detailItem {
                            margin-bottom: 5px;                                
                            &:after {
                                @include clearBoth;
                            }
                            .dashedImg {
                                background: url(../img/dashed.png) center center repeat;
                                height: 1px;
                            }
                        }
                        .tableTitle {
                            @include fontSize(12px,400);
                            color: #333;
                            line-height: 18px;
                            float: left;
                            width: 67px;
                        }
                        .goodsContent {
                            &.total {
                                @include fontSize(12px,500);     
                                span {
                                    @include fontSize(14px,700);
                                    line-height: 17px;                                    
                                }
                            }
                            &.num {
                                @include fontSize(14px,700);
                                color: $mainColor;
                                line-height: 17px;
                            }
                            &.device {
                                @include fontSize(12px,500);
                                color: $mainColor;                            
                            }
                            @include fontSize(12px,500);
                            color: #000;
                            line-height: 18px;
                            float: right;
                            width: calc( 100% - 107px );
                            text-align: right;
                            span {
                                @include fontSize(12px,500);
                                line-height: 14px;                                    
                            }
                        }    
                    }
                    .notice-area {
                        text-align: center;
                        padding-bottom: 40px;
                        .orderNum {
                            @include fontSize(17px,700);
                            color: #000;
                            line-height: 25px;
                        }
                        .subText {
                            @include fontSize(12px,300);
                            color: #000;
                            line-height: 18px;
                        }
                    }
                }               
                .footer-section {
                    button {
                        border: 0;                            
                        height: 40px;
                        width: 100%;
                        background: $mainColor;
                        color: #ffffff;
                        @include fontSize(14px,500);                          
                    }
                }
            }    
        }
        &.access-wrapper {
            .popup-inner {
                .header-section {
                    padding-left: 15px;
                    p {
                        text-align: left;
                        @include fontSize(18px,700);
                        color: #000;
                        line-height: 26px;
                    }
                }         
                .content-section {    
                    padding: 0 15px 30px;
                    .allowInfo {
                        padding: 10px 5px;
                        border-bottom: 1px solid #eeeeee;
                        &:last-of-type {
                            border: 0;
                        }
                        &:after {
                            @include clearBoth;
                        }
                        img {
                            float: left;
                            margin-right: 10px;
                            width: 20px;
                        }
                        .text-area {
                            float: left;
                            .main-text {
                                @include fontSize(14px,700);
                                color: #000;
                                line-height: 18px;
                                margin-bottom: 4px;
                            }
                            .sub-text {
                                @include fontSize(12px,400);
                                color: #000;
                                line-height: 18px;
                            }
                        }
                    }                
                }
                .footer-section {
                    button {
                        border: none;
                        background: $mainColor;
                        width: 100%;
                        height: 40px;
                        @include fontSize(14px,500);
                        color: #ffffff;                        
                    }                    
                }                
            }
        }
        &.purchase-wrapper {
            padding: 0;
            .popup-inner {     
                position: fixed;
                bottom: 0;
                .header-section {
                    width: calc( 100% - 25px );
                    padding: 15px;
                    p {
                        text-align: left;
                        color: #000;
                        @include fontSize(17px,500);
                        line-height: 25px;
                    }
                }  
                .content-section {
                    border-top: 1px solid #eee;
                    &:after {
                        @include clearBoth;
                    }
                    padding: 20px 30px;
                    position: relative;
                    .btnBlock {     
                        &:after {
                            @include clearBoth;
                        }
                        >button {
                            float: left;
                            background: none;
                            border: 1px solid #eee;
                            width: 30px;
                            height: 28px;
                            img {
                                width: 10px;
                            }
                        }
                        .goodsAmount {
                            float: left;
                            width: 47px;
                            height: 28px;
                            border: 1px solid #eee;
                            @include flexAllCenter;
                            span {
                                color: #333;
                                @include fontSize(12px,400);
                                line-height: 14px;
                            }
                        }
                    }
                    .priceBlock {                 
                        @include flexCenter; 
                        position: absolute;
                        right: 30px;
                        top: 50%;
                        transform: translate(0,-50%);
                        &::after {
                            @include clearBoth;
                        }
                        .totalText {
                            @include fontSize(12px,500);
                            color: #333;
                            line-height: 18px;
                            margin-right: 5px;
                            float: left;
                        }
                        .totalNum {
                            @include fontSize(14px,500);
                            color: #333;
                            line-height: 20px;
                            float: left;
                            span {
                                @include fontSize(18px,700);
                                color: #333;
                                line-height: 22px;
                            }
                        }
                    }
                }
                .footer-section {
                    button {
                        width: 100%;
                        height: 60px;
                        background: $mainColor;
                        color: #fff;
                        @include fontSize(15px,500);
                        border: none;
                    }
                }
            }
        }
        &.payment-wrapper {
            padding: 0;
            .popup-inner {
                width: 100%;
                height: 100vh;
                overflow: scroll;
                .close-box {
                    width: 49px;
                    height: 49px;
                    img {
                        width: 19px;
                    }
                }
                .header-section {
                    height: 54px;
                    padding: 0;
                    padding-left: 15px;
                    @include flexCenter;
                    p {
                        @include fontSize(20px,500);
                        text-align: left;
                    }
                }
                .content-section {
                    min-height: calc( 100vh - 181px );
                    .price-title {
                        padding: 10px 15px 0;
                        &:after {
                            @include clearBoth;
                        }
                        p {
                            float: left;
                            line-height: 28px;
                            @include fontSize(14px,400);
                            color: #333;
                        }
                        strong {
                            float: right;
                            @include fontSize(12px,400);
                            color: $mainColor;
                            span {
                                @include fontSize(23px,700);
                                color: $mainColor;
                                margin-right: 2px;
                            }
                        }
                    }
                    .price-container {
                        padding: 13px 0;
                        .swiper-wrapper {
                            .swiper-slide {
                                width: calc( 100% - 30px );
                                box-shadow: 0 3px 15px 0 rgba(0, 0, 0, 0.1);
                                border: solid 1px #dcdcdc;
                                padding-top: 8px;
                                position: relative;
                                .numberling {
                                    @include flexCenter;
                                    position: absolute;
                                    top: 15px;
                                    right: 15px;
                                    li {
                                        color: #fff;
                                        @include fontSize(14px,400);
                                        text-indent: -9999px;
                                        width: 10px;
                                        height: 10px;
                                        border-radius: 10px;
                                        @include flexAllCenter;
                                        line-height: 19px;
                                        background: #dcdcdc;
                                        margin-right: 5px;
                                        &:last-child {
                                            margin-right: 0;
                                        }
                                        &.active {
                                            width: 20px;
                                            height: 20px;
                                            background: $mainColor;
                                            text-indent: 0;
                                        }
                                    }
                                }
                                .table-wrapper {
                                    padding: 25px 15px;
                                    border-bottom: 1px solid #eee;
                                    &:first-child {
                                        padding-bottom: 15px;
                                    }
                                    .table-title {
                                        margin-bottom: 5px;
                                        p {
                                            @include fontSize(14px,700);
                                        }
                                    }
                                    .table-content {
                                        li {
                                            @include fontSize(14px,400);
                                            line-height: 20px;
                                            color: #333;
                                            position: relative;
                                            strong {
                                                position: absolute;
                                                right: 0;
                                                top: 0;
                                                line-height: 20px;
                                                @include fontSize(14px,500);
                                                &.price {
                                                    @include fontSize(14px,400);
                                                    span {
                                                        @include fontSize(14px,700);
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                .order-detail {
                                    padding: 25px 15px 23px;
                                    background: #fff9f9;
                                    >span {
                                        background: #000;
                                        padding: 0 14px;
                                        color: #fff;
                                        @include fontSize(15px,400);
                                        line-height: 33px;
                                        display: inline-block;
                                        margin-bottom: 15px;
                                    }
                                    .order-number {
                                        @include fontSize(17px,700);
                                        line-height: 25px;
                                        margin-bottom: 5px;
                                        span {
                                            @include fontSize(19px,700);
                                            margin-left: 5px;
                                        }
                                    }
                                    .order-counting {
                                        @include fontSize(14px,400);
                                        line-height: 22px;
                                        margin-bottom: 5px;
                                        span {
                                            @include fontSize(18px,700);
                                            color: $mainColor;
                                            margin-right: 5px;
                                            &:last-child {
                                                margin-right: 0;
                                            }
                                        }
                                    }
                                    .warning-text {
                                        @include fontSize(14px,400);
                                        line-height: 20px;
                                        span {
                                            color: $mainColor;
                                            @include fontSize(14px,700);
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}