.content-body {  
    &.pickup-content{  
        padding-bottom: 0;
        .subMenu-container {
            margin-top: 10px;
            &:after {
                @include clearBoth;
            }
            .select-box {
                float: right;
                position: relative;
                padding-right: 17px;
                &:after {
                    width: 10px;
                    height: 8px;
                    content: "";
                    position: absolute;
                    background: url(../img/icon_select01.png) center center no-repeat;
                    background-size: cover;
                    right: 0;           
                    top: 50%;                    
                    transform: translate(0,-50%);
                }
                .listUp-btn {
                    border: 0;
                    height: 18px;
                    @include fontSize(12px,500);
                }
            }
        }
        .pickupList-container {    
            margin-top: 5px;
            .none-list {
                @include fontSize(12px,500);
                color: #999;
                text-align: center;
                line-height: calc( 100vh - 87px );
            }
            .pickup-wrapper {
                @include flexCenter;
                padding: 20px 0;
                border-bottom: 1px solid #eeeeee;
                &:first-child {
                    border-top: 1px solid #eee;
                }
                .img-box {
                    background: url(../img/image_09.png) center center;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                }
                .word-box {
                    height: 80px;
                    width: calc( 100% - 90px);
                    position: relative;   
                    &.cancle {
                        opacity: 0.5;
                    }
                    .date {      
                        margin-bottom: 7px;        
                        &:after {
                            @include clearBoth;
                        }
                        p {
                            font-size: 12px;
                            color: #333333;
                            float: left;
                            line-height: 14px;
                        }
                        button {
                            border: 0;
                            background: none;
                            float: right;
                            height: 7px;
                            top: 50%;                    
                            transform: translate(0,-50%);
                        }
                    }
                    .menuInfo {
                        p {
                            color: #333;
                            @include fontSize(14px,500);
                            line-height: 20px;
                            @include flexCenter;
                            @include wordClear;
                            width: 100%;
                            padding-right: 5px;
                            span {
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 18px;
                                background: #000;
                                padding: 3px 9px;
                                border-radius: 12px;
                                margin-right: 5px;
                                display: inline-block;
                            }
                        }
                    }
                    .price { 
                        position: absolute;
                        width: 100%;
                        bottom: -2px;
                        &:after {
                            @include clearBoth;
                        }                   
                        .select-status {
                            float: left;
                            span {
                                color: $mainColor;
                                @include fontSize(12px,500);
                                line-height: 18px;
                            }
                        }
                        .priceNum {
                            float: right;
                            color: #333;
                            @include fontSize(12px,400);
                            span {
                                color: #333;
                                @include fontSize(15px,700);
                                line-height: 18px;
                            }
                        }
                    }
                }
            }
        }
    }
}

